html {
    scroll-behavior: smooth;
}
body {
    background-color: $bg-color;
    color: $text-color;
}

button {
    cursor: pointer;
}

.container {
    width: calc(100% - #{$container-padding-desktop * 2});
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    min-height: 100vh;

    @media (max-width: 768px) {
        width: calc(100% - $container-padding-desktop);
    }

    @media (max-width: 460px) {
        width: calc(100% - #{$container-padding-desktop * 0.6});
    }
}

.main {
    flex: 1;
    margin-bottom: 120px;
}

body.no-scroll {
    overflow: hidden;
}